body {
  touch-action: none;
  -webkit-user-select: none;
  user-select: none;
  color: #000;
  background-color: #fff;
  width: 100svw;
  height: 100svh;
  position: fixed;
  overflow: hidden;
}

@media (prefers-color-scheme: dark) {
  body {
    color: #fff;
    background-color: #222;
  }
}

#gesture-area {
  width: 100svw;
  height: 100svh;
  position: absolute;
}

#gesture-area img {
  transform-origin: 0 0;
  width: auto;
  max-width: none;
  position: absolute;
}

#gesture-area img#img-ref {
  border: .2svw solid gray;
}

#gesture-area img[src="data:,"] {
  display: none;
}

.control {
  flex-wrap: wrap;
  display: flex;
  position: absolute;
}

.control > * {
  flex-grow: 1;
}

.control input[type="file"] {
  display: none;
}

.control label, .control button {
  color: #000;
  cursor: pointer;
  background-color: #ffffff80;
  border: 1px solid #ccc;
  padding: 6px 12px;
  display: inline-block;
}

.control label:has( > input[type="checkbox"]:checked) {
  background-color: #7f7f7f80;
}

@media (prefers-color-scheme: dark) {
  .control label, .control button {
    color: #fff;
    background-color: #00000080;
  }
}
